import React, {Component} from 'react';
import PureRenderMixin from 'react-addons-pure-render-mixin'

import {Anchor,Row,Col,Breadcrumb,Button, Input} from 'antd';
const Search = Input.Search;
import {withRouter} from 'react-router-dom'


import './style.scss';



class Content extends Component {
    constructor(props) {
        super(props);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this)
        this.state = {
            showAll: "block",
            headerNav: [{
                name: ""
            }, {
                name: ""
            }, {
                name: ""
            }],

        }
    }

    toggleLogin = () => {
        this.props.logFn();
    }

    getChainsList = () => {
        const data = this.props.chainsList;

        if(data && data.length) {
            return data
        }

    }

    tableView(data, id){
        console.log(id)
        this.props.showChainsTable(data, id);
    }


    hideShowButton = (index, types) => {
        this.props.showAndHide(index, types);
    }

    getContractShopList = () => {
        let data = this.props.contractShopList;

        return data;
    }

    goBack = () => {
        location.replace("/#/platform")
    }

    goTestChains = () => {
        this.props.history.push("/platform/join");
    }

    render() {
        return (
            <Row type="flex" justify="center" className="verify-content">
                <Col className="top" span={16}>
                    <Breadcrumb separator=">">
                        <Breadcrumb.Item className="BreadcrumbItem" href="/#/"></Breadcrumb.Item>
                        <Breadcrumb.Item className="BreadcrumbItem" href="/#/platform"></Breadcrumb.Item>
                        <Breadcrumb.Item className="BreadcrumbItem" href="/#/platform/contract/develop"></Breadcrumb.Item>
                        <Breadcrumb.Item className="BreadcrumbItem" href="/#/platform/contract/upload"></Breadcrumb.Item>
                    </Breadcrumb>

                    <h2 className="title">  ...</h2>

                    <Row type="flex" justify="center" className="chains-intent">
                        <Col span={18} className="content">
                            <div className="section">
                                <div>
                                    <span className="label">:</span>
                                    <span>{this.props.uploadContractInfo.name}</span>
                                </div>
                                <div>
                                    <span className="label">ID:</span>
                                    <span>{this.props.uploadContractInfo.id}</span>
                                </div>
                                <div className="pulisher">
                                    <span className="label">:</span>
                                    <span>{this.props.uploadContractInfo.publisher}</span>
                                </div>
                            </div>

                        </Col>
                    </Row>
                    <Row type="flex" justify="center">
                        <Col span={18} className="prompt">
                            <span className="skip" onClick={this.goTestChains}></span>
                        </Col>
                    </Row>
                    <div className="button-content">
                        <Button className="Back" onClick={this.goBack}></Button>
                    </div>



                </Col>
            </Row>


        )
    }
}

export default withRouter(Content);


