import React, {Component} from 'react';
import PureRenderMixin from 'react-addons-pure-render-mixin'

import {Row, Col} from 'antd';
import {HashRouter as Router, Link} from 'react-router-dom'

import './style.scss';


import Headerlogo from 'images/headerlogo.png';



class PlatformHeader extends Component {
    constructor(props) {
        super(props);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
        this.state = {
            headerNav: [{
                name: ""
            }, {
                name: ""
            }, {
                name: ""
            }],

        }
    }


    Gohome = () => {
        location.replace("/")
    }

    render() {
        return (
            <header className="platform-header">
                <Row type="flex" justify="center" className="header-content">
                    <Col className="top" span={16}>
                        <img src={Headerlogo} alt="logo" onClick={this.Gohome} />
                        {
                            window.screen.width < 768 ?

                                ""
                                :
                                <Router>
                                    <ul className="header-nav">
                                        {
                                            this.props.page == "Platform" ? <li className="header-nav-item"><Link className="link" to="/"></Link></li> : null
                                        }
                                    </ul>
                                </Router>
                        }
                    </Col>
                </Row>
                {/*<div className="bg">*/}
                    {/*<p className="slogen"></p>*/}
                {/*</div>*/}

            </header>

        )
    }
}

export default PlatformHeader;


